.PageMain {
  padding: 0 20px;
  user-select: none;

  .PageMain__head {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;

    .PageMain__head__title {
      font-size: 30px;
      margin: 0;
      padding: 0;
      transform: translateY(-300%);
      animation: PageMain-translate 1s forwards;
    }

    .PageMain__head__input {
      display: none;
    }

    .PageMain__head__label {
      position: relative;
      cursor: pointer;
      border: solid 2px white;
      border-radius: 15px;
      padding: 10px;
      display: block;
      width: 140px;
      transition: 1s;

      &:hover {
        transform: scale(1.1);
      }

      &:active {
        transition: 0.2s;
        transform: scale(0.9);
        background: rgba(132, 255, 0, 0.4);
        border-color: rgba(132, 255, 0, 0.601);
      }
    }
  }

  .PageMain__head__input:checked + .PageMain__head__label::after {
    content: " on";
    z-index: 100;
    font-weight: 700;
    width: 100%;
    text-align: center;
    color: rgb(60, 213, 0);
  }

  .PageMain__head__input + .PageMain__head__label::after {
    content: " off";
    z-index: 100;
    font-weight: 700;
    width: 100%;
    text-align: center;
    color: red;
  }

  .PageMain__cards {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 30px;
    padding: 30px;

    .PageMain__cards__controls {
      width: 100%;
      display: flex;
      gap: 40px;
      flex: wrap;
      align-items: center;
      justify-content: center;

      .PageMain__cards__controls__page {
        margin: 0;
        padding: 0;
        width: 40px;
        font-size: 20px;
        font-weight: 700;
        text-align: center;
      }
    }
  }

  .PageMain__load {
    position: fixed;
    z-index: 150;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    max-width: 300px;
    width: 100%;
  }

  .PageMain__load_hide {
    display: none;
  }

  .PageMain__btn {
    background: none;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    border: solid 2px white;
    color: white;
    font-size: 30px;
    font-weight: 700;
    transition: 1s;

    &:hover {
      cursor: pointer;
      transform: scale(1.1);
      background: rgba(132, 255, 0, 0.4);
      border-color: rgba(132, 255, 0, 0.601);
    }

    &:active {
      transform: scale(0.9);
      transition: 0.2s;
    }

    &:disabled {
      cursor: default;
      transform: scale(1);
      background: none;
      border-color: gray;
      color: gray;
    }
  }

  .to-top {
    position: fixed;
    right: 30px;
    bottom: 30px;
  }

  .to-top_hide {
    display: none;
  }
}

@keyframes PageMain-translate {
  0% {
    transform: translateY(-300%);
  }
  100% {
    transform: translateY(0);
  }
}
